export const InputModes = {
	Motion: 0,
	Keyboard: 1,
	Mouse: 2,
}

let version = 0

export default class AppInput {

	constructor(requestedMode) {
		this._rawData = {
			version: -1,
			gamma: 0,
			delta: 0,
			alpha: 0,
		}

		if (window.DeviceOrientationEvent) {
			alert("Adding listener")
			this._inputMode = requestedMode || InputModes.Motion
			window.addEventListener("deviceorientation", this._newData, true)
		}else{
			this._inputMode = requestedMode || InputModes.Keyboard
			alert("Sorry, your browser doesn't support Device Orientation")
		}
	}

	_newData(data) {
		data.version = version++

		console.log(`input mode: ${this.mode()} data: `, data)

		this._rawData = data
	}

	mode = () => {
		for (let key in InputModes) {
			if (this._inputMode === InputModes[key]) {
				return key
			}
		}

		return InputModes[this._inputMode]
	}

	get data() {

		const rd = this._rawData

		return {
			version: rd.version,
			red: rd.gamma,
			blue: rd.delta,
			green: rd.alpha,
		}
	}

	get version() {
		return version
	}

	_gyroError = (event) => {
		this._inputMode = InputModes.Keyboard
		console.log(`gyro error: ${event}. switching to ${this.mode()}`)
	}
}


